import React, { useState, useEffect } from 'react';
import '../styles/Variables.scss';
import '../styles/DeliveryDisclaimer.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import {useCart} from "../context/CartContext";


const DeliveryDisclaimer = () =>{
    const {billTotal} = useCart()
    const [costif, setCostIf] = useState(20)
    useEffect(() => {
        if(billTotal()>20){
            console.log("free");
        }else if(billTotal()>0){
            setCostIf(20-billTotal())
            console.log("free if" + costif);
        }else{
            console.log("bill is zero");

        }
    }, [billTotal()])
 
    
    return(
        <div className="delivery-disclaimer-wrapper">            
            <div className="delivery-disclaimer-container">                      
                {(billTotal()==0)&&<div className="delivery-disclaimer-free-delivery-cost">Για όλες τις παραγγελίες άνω των 20&euro; τα μεταφορικά είναι δωρεάν.</div>}
                {(billTotal()>19.9999)&&<div className="delivery-disclaimer-win-delivery-msg"><b>Κέρδισες δωρεάν μεταφορικά με αυτή τη παραγγελία.</b></div>}
                {(billTotal()>0)&&(billTotal()<20)&&<div className="delivery-disclaimer-second-message">Έχεις χρεωθεί 5&euro; μεταφορικά.<br/><b>Προσθέστε προϊόντα αξίας {costif}&euro;</b> για να κερδίσετε δωρεάν μεταφορικά.</div>}
            </div>
        </div>
    )
}

export default DeliveryDisclaimer;